<script>
import axios from 'axios';
import DefaultConfig from '../../DefaultConfig.vue';
axios.interceptors.response.use(response => {
    return response;
}, error => {
    if (error.response.status === 401) {
        location.hash = '/Hypergryph/Login'
        localStorage.setItem("Cred_info", false);
    }
    return error;
});

const check = function () {
    axios
        .get("https://zonai.skland.com/api/v1/user/check", {
            headers: {
                "cred": localStorage.getItem('Cred'),
                "platform": "1",
            }
        })
        .then((response) => {
            if (response.data.message == "OK") {
                localStorage.setItem("Cred_info", true);
            }
        }).catch(() => {
        });
}

function bulletins(gameid) {

    axios
        .get("https://zonai.skland.com/api/v1/bulletins/list?gameId=" + gameid + "&cateId=3", {
            headers: {
                "cred": localStorage.getItem('Cred'),
                "platform": "1",
            }
        })
        .then((response) => {
            return response.data
        });
}


