<template>
    <div>
        <transition name="el-fade-in-linear">
            <el-row id="news" :gutter="20" justify="end" key="action3" v-infinite-scroll="getlist" v-masonry
                transition-duration="0.2s" :infinite-scroll-distance="10">
                <transition-group name="el-fade-in-linear">
                    <el-col v-for="(item, index) in data" :lg="12" :md="21" :xl="12" :xs="24" :sm="24" :key="index"
                        v-masonry-tile ::key="index" class="mb-2">

                        <el-tooltip placement="auto" effect="light" trigger="click">
                            <template #content>
                                <el-text v-for="(text, index)  in item.item.textSlice" :key="index">{{ text.c
                                }}
                                    <br /></el-text>

                                <p> <b><a :href="'https://www.skland.com/h/detail?id=' + item.item.id" target="_blank">
                                            前往页面</a></b></p>

                            </template>
                            <div class="newsbg">
                                <img :src="item.item.imageListSlice.length > 0 ? item.item.imageListSlice[0].url : ''">
                                <el-button class="addto" type="danger" @click="addtoHome(item, index)" circle><el-icon>
                                        <Star />
                                    </el-icon></el-button>
                                <span>{{ item.item.title }}</span>
                            </div>
                        </el-tooltip>

                    </el-col>
                </transition-group>
                <el-col :span="24" style="height: 120px;">
                </el-col>
            </el-row>
        </transition>
        <el-row id="banner">
            <el-col>
                <el-carousel trigger="click" height="100px">
                    <el-carousel-item v-for="(item ) in  carousels " :key="item">
                        <a target="_blank"
                            :href="decodeURIComponent((item.link + '').replace('skland://', '')).split('web?url=').length > 1 ? decodeURIComponent((item.link + '').replace('skland://web?url=', '')) : 'https://www.skland.com/h/' + decodeURIComponent((item.link + '').replace('skland://', ''))">
                            <img :src="item.content.cover.url">
                        </a>
                    </el-carousel-item>
                </el-carousel>
            </el-col>
            <el-col>
                <el-tabs v-model="type" class="h-0 mt-3 p-0" stretch @tab-change="getlist()">
                    <!-- <el-tab-pane label="活动" name="2" /> -->
                    <el-tab-pane label="推荐" name="16" />
                    <el-tab-pane label="官方" name="12" />
                </el-tabs>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import axios from 'axios';
import HypergryphVue from './Hypergryph/Hypergryph.vue';

export default {
    name: 'GameInfoDynamicEndfield',
    HypergryphVue,
    data() {
        return {
            carousels: [],
            type: "12",
            gameid: 3,
            data: [],
            pagenum: -1,
            game: 'skland',
            pageToken: '',
            lasttype: "",
            templock: true
        };
    },

    mounted() {
        HypergryphVue.check()
        this.bulletins()
        this.getlist()
    },

    methods: {
        addtoHome(item) {
            // if (this.type == "2" || this.type == "1") {
            //     item.content = item.content.replaceAll('miHoYoGameJSSDK.openInBrowser', 'window.open').replaceAll('miHoYoGameJSSDK.openInWebview', 'window.open');
            // }

            console.log(this.game)
            item.local = {
                url: 'https://www.skland.com/h/detail?id=' + item.item.id,
                countdown: null,
                game: this.game,
                type: null,
            }
            if (localStorage.getItem("Homedata") != null) {
                var data = JSON.parse(localStorage.getItem("Homedata"));
                data = data.concat(item);
                localStorage.setItem("Homedata", JSON.stringify(data));
            } else {
                var data2 = [];
                data2 = data2.concat(item);
                localStorage.setItem("Homedata", JSON.stringify(data2));
            }

            this.$notify.success({
                message: '添加到主页成功',
                position: "top-left",
                duration: 1000,
                showClose: false
            })


        },
        relink(link) {
            var data = link + '';
            return data.replace()
        },
        bulletins() {
            var _this = this;
            axios
                .get("https://zonai.skland.com/api/v1/bulletins/list?gameId=" + this.gameid + "&cateId=16", {
                    headers: {
                        "cred": localStorage.getItem('Cred'),
                        "platform": "1",
                    }
                })
                .then((response) => {
                    const data = response.data.data.list;
                    for (let index = 0; index < data.length; index++) {
                        if (data[index].content.cover.url != "") {
                            _this.carousels = _this.carousels.concat(data[index])
                        }
                    }
                    _this.bulletins2()

                }).catch(() => {
                });
        },
        // bulletins2() {
        //     var _this = this;
        //     axios
        //         .get("https://zonai.skland.com/api/v1/bulletins/list?gameId=" + this.gameid + "&cateId=2", {
        //             headers: {
        //                 "cred": localStorage.getItem('Cred'),
        //                 "platform": "1",
        //             }
        //         })
        //         .then((response) => {
        //             const data = response.data.data.list;
        //             for (let index = 0; index < data.length; index++) {
        //                 if (data[index].content.cover.url != "") {
        //                     _this.carousels = _this.carousels.concat(data[index])
        //                 }
        //             }
        //         });
        // },
        getlist() {
            if (this.templock) {
                // https://zonai.skland.com/api/v1/rec/index?gameId=1&cateId=14&sortType=1&pageToken=1&pageSize=10            
                var _this = this;

                _this.templock = false


                if (this.lasttype != this.type) {
                    this.data = []
                    this.lasttype = this.type;
                }


                axios
                    .get("https://zonai.skland.com/api/v1/" + (this.type == "12" ? "home" : "rec") + "/index?gameId=" + this.gameid + "&cateId=" + _this.type + "&sortType=2&pageToken=" + this.pageToken + "&pageSize=10 ", {
                        headers: {
                            "cred": localStorage.getItem('Cred'),
                            "platform": "1",
                        }
                    })
                    .then((response) => {
                        _this.data = _this.data.concat(response.data.data.list)
                        _this.pageToken = response.data.data.pageToken;
                        _this.templock = true;
                    }).catch(() => {
                        _this.templock = true;
                    });
            }

        }
    },
};
</script>



<style >
#banner {
    width: 400px;
    position: fixed;
    left: 25px;
    bottom: 200px;
}

#banner .el-carousel {
    box-shadow: 0px 0px 40px 0px black;
    border-radius: 20px;
}

#banner .el-tabs__header {
    backdrop-filter: blur(3px);
    background-color: rgba(0, 0, 0, 0.514);
    box-shadow: 0px 0px 40px 0px black;
    border-radius: 10px;
}

#banner .el-tabs__item.is-active {
    color: black !important;
    transition: all 0.3s;
}

#banner .el-tabs__active-bar {
    border-radius: 10px;
    height: 100%;
    z-index: -1;
    background-color: #fffffff0;
    backdrop-filter: blur(3px);
}

.el-popper {
    max-width: 50%;
    max-height: 80%;
    overflow: scroll;
    background: rgba(255, 255, 255, 0.74) !important;
    box-shadow: 0px 0px 40px 0px black;
    border: none !important;
    backdrop-filter: blur(5px);
}


.addto {
    position: absolute;
    top: 10px;
    right: 10px;
    box-shadow: 0px 0px 40px 0px black;
    background-color: #f56c6ca1;
    visibility: hidden;
    transition: all 0.3s;
    opacity: 0
}

#banner {
    width: 400px;
    position: fixed;
    left: 25px;
    bottom: 200px;
}

